@font-face {
    font-family: Roboto;
    src: url('https://fonts.googleapis.com/css?family=Roboto');
}

.header{
    background-color: #000;
    display: block;
    padding-left: 16px;
    font-family: Roboto, serif;
}

.header-input{
    margin-top: 10px;
    right: 1px;
    font-size: 24px;
    display: inline-block;
    width: 220px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 16px;
    font-family: Roboto, serif;
}

.header-name {
    padding-left: 20px;
    margin-top: -20px;
    display: inline-block;
    color: #fff;
    font-family: Roboto, sans-serif;
}

.header-home{
    font-size: 20px;
    padding-left: 30px;
    display: inline-block;
    color: #fff;
    font-family: Roboto, sans-serif;
}

.select-form{
    font-family: Roboto, sans-serif;
    font-size: 16px;
    background-color: #000;
    color: white;
    border-color: black;
    border-width: 3px;
    width: 125px;
    height: 25px;

}